<template>
  <div
    class="tab_bar_item"
    @click="tab_bar_item_click"
    :style="showActiveColor"
  >
    <div><slot name="icon"></slot></div>
    <div><slot name="txt"></slot></div>
  </div>
</template>

<script>
export default {
  props: {
    path: {
      type: String,
    },
    activeColor:{
      type: String,
      default: 'deeppink'
    }
  },
  data() {
    return {
      
    };
  },
  computed:{
    isActive(){
      return this.$route.path.indexOf(this.path) !== -1
    },
    showActiveColor(){
      return this.isActive ? { color: this.activeColor} : {}
    }
  },
  mounted(){
    // console.log(this.$route)
  },
  methods: {
    tab_bar_item_click() {
      this.$router.push(
        {
          path: this.path,
        },
        (onComplete) => {},
        (onAbort) => {}
      );
      
      console.log(this.isActive);
    },
  },
};
</script>

<style scoped>
.tab_bar_item {
  flex: 1;
  text-align: center;
  /* color: deeppink; */
}
</style>