export default {
    addCart(context, payload) {
        return new Promise((resolve, reject) => {
            let oldGoods = context.state.cartList.find(item => item.iid === payload.iid) //判断是否已经有添加过的商品
            if (oldGoods) {
                //已经有添加的话只需要将count++
                context.commit('addCounts', oldGoods)
                resolve('添加成功，在购物车等亲~')
            } else {
                //还没添加的话追加到数组后面
                payload.count = 1
                context.commit('addToCart', payload)
                resolve('添加成功，在购物车等亲~')
            }
        })

    }
}