<template>
  <div class="toast" v-show="isShow">
    <div class="toast_mag">{{message}}</div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      message: '',
      isShow: false
    }
  },
  methods:{
    showToast(message="默认文字", duration=1500){
      this.isShow = true
      this.message = message

      setTimeout(() => {
        this.isShow = false
      }, duration);
    }
  }
}
</script>

<style scoped>
.toast{
  position: fixed;
  top: 50%;
  left: 50%;
  background: rgba(0,0,0,.7);
  color: #fff;
  line-height: 32px;
  padding: 8px 16px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 4px;
  text-align: center;
  box-sizing: border-box;
  z-index: 9999;
  transform: translate(-50%,-50%);
}
</style>