/*
 * @name: 
 * @test: test font
 * @msg: 
 * @param: 
 * @return: 
 */
export default {
    mode: 'hash',
    routes: [{
            path: '*',
            redirect() { //路由重定向
                return '/home'
            },
        },
        {
            path: '/home',
            name: '首页',
            component: () =>
                import ('@/views/home/Home.vue')
        }, ,
        {
            path: '/category',
            name: '分类',
            component: () =>
                import ('@/views/category/Category.vue')
        },
        {
            path: '/cart',
            name: '购物车',
            component: () =>
                import ('@/views/cart/Cart.vue')
        },
        {
            path: '/mine',
            name: '我的',
            component: () =>
                import ('@/views/mine/Mine.vue')
        },
        {
            path: '/detail/:iid',
            name: "详情页",
            component: () =>
                import ('@/views/detail/Detail.vue')
        }

    ]
}